@font-face {
     font-family:"ProximaNova";
     src:url(../fonts/ProximaNova-Bold.ttf)format("truetype");
     src:url(../fonts/ProximaNova-Bold.eot)format("embedded-opentype");
     src:url(../fonts/ProximaNova-Bold.woff)format("woff");
     font-style: normal;
     font-weight: 700;
}
