@import url('app/less/_normalize.less');
@import url('app/less/_fonts.less');
@import url('app/less/_variables.less');

html, body, section {
	overflow-x: hidden;
}
body 
{
	margin: 0;
	padding: 0;
	font-family: 'Roboto Condensed', sans-serif;
	font-weight: @fw;
	font-size: 20px;
	color: #ffffff;
	position: relative;

}

body *::selection {
		background: rgba(13, 3, 229, 0.8);
	}


body:before {
	    position: absolute;
    bottom: 0;
    content: "";
    background: url(../img/man.png) center/contain no-repeat;
    width: 100%;
    max-height: 54%;
    height: 100%;
    left: 3.9%;
    z-index: 3;
    pointer-events: none;

}



*, *:before, *:after {
	box-sizing: border-box;
}

img
{
	max-width: 100%;
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

ul , li {
	list-style: none;
	padding: 0;
	margin: 0;
}

section.main {
	background: url(../img/main_bg.jpg) no-repeat center;
	background-size: cover;

}

input {
	width: 100%;
	outline: none;
	border: none;
	font-family: 'Roboto Condensed', sans-serif;
	text-align: left;
	width: 238px;

}


.center {
	max-width: 1230px;
	width: 100%;
	height: 100%;
	margin: 0 auto;
	padding: 0 30px;
	// padding-top: 30px;
}

.flex-container {
	// padding: 30px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: nowrap;
	min-height: 100vh;
	height: 100%;
	flex-direction: row;
}

.content_left {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	flex-wrap: nowrap;
	z-index: 9;
	    flex: 0 1 620px;
}

.content_right {
	   //  -webkit-box-flex: 1;
    // -webkit-flex: 1 0 370px;
    // -moz-box-flex: 1;
    // -ms-flex: 1 0 370px;
    // flex: 1 0 370px;
    min-width: 400px;
    padding-left: 25px;
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
}

.content_right:before {
	    position: absolute;
    bottom: 0%;
    content: "";
    background: url(../img/man.png) center/contain no-repeat;
    width: 100%;
    /* max-height: 100%; */
    height: 36%;
    min-height: 613px;
    left: -67%;
    z-index: 8;
    pointer-events: none;

}


p.free {
	font-weight: @fwb;
	margin-bottom: 57px;
	font-size: 30px;
}

h1.title {
	font-weight: @fwb;
	font-size: 38px;
	margin-bottom: 40px;

}

h2.sub_title {
	margin-bottom: 20px;
	font-size: @sub_header;
	font-weight: 300;
	opacity: 0.7;

}


h2.sub_title:nth-child(2) {
		margin-bottom: 	10px;
}

.list_item {
	position: relative;
	line-height: 1.55;
	opacity: 0.7;
	padding-left: 25px;
	& span {
		font-weight: @fwb;
	}
	&:before {
		position: absolute;
		content: "";
		width: 8px;
		height: 8px;
		border-radius: 50%;
		top: 10px;
		left: 0;
		background: #fff;
	}
}
.logo {
	margin-bottom: 45px;
}

.form_wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	flex-wrap: nowrap;
	background: #0f0f0f;
	padding: 35px;
	max-width: 310px;
	margin: 0;
	margin-left: auto;
	border-radius: 7px;
	border: 1px solid #aa384a;

	.form_text {
		text-align: center;
		color: #ffffff;
		margin-bottom: 30px;
		opacity: 0.7;
	}
}


.main_form {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	flex-wrap: nowrap;
	width: 100%;
}

.text_field {
	border: 1px solid #878787;
	background: transparent;
	margin-bottom: 18px;
	border-radius: 7px;
	font-size: 17px;
	color: #fff;
	padding: 10px  0  10px 25px;
	&::-webkit-input-placeholder { 
		color: #1a1a1a;; 
		opacity: 1;
	}
	&::-moz-placeholder { 
		color: #1a1a1a;; 
		opacity: 1;
	}
}

.main_list {
	max-width: 525px;
}

.btn_submit {
	padding: 10px 0;
	background: linear-gradient(to bottom, #482442, #60284a, #7a2c4e, #93314e, #aa384a);
	cursor: pointer;
	transition: all 0.4s linear;
	text-align: center;
	border-radius: 7px;
	font-weight: 300;
	color: #ffffff;
	opacity: 0.8;
	font-size: 17px;
	box-shadow: -10px 12px 58px 0px rgba(170,56,74,1);
	&:hover {

		background: linear-gradient(to top, #482442, #60284a, #7a2c4e, #93314e, #aa384a);
	}
}


.form_wrapper-main, .form_wrapper-thanks {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: column;
	flex-wrap: nowrap;
	width: 100%;
}




.form_wrapper-thanks {
	color: #ffffff;
	text-align: center;
	display: none;
	opacity: 0;
	justify-content: center;
	height: 308px;
}


.form_thanks-title h2{
	font-size: 30px;
	font-weight: @fwr;
	margin-bottom: 20px;
}

p.form_thanks_topt, p.form_thanks_lowt {
	margin-bottom: 50px;
	font-size: 17px;
}

.form_thanks-social > div {
	display: inline-block;
}

.fb_social {
	margin-right: 16px;
}

@media screen and (max-width: 1130px) {
	.main_list {
		background: rgba(0,0,0, 0.6);
		padding: 15px;
		border-radius: 15px;
	}

	// .list_item {
	// 	opacity: 1;
	// }
	
}


@media screen and (max-width: 440px) {
	// .content_right {
	// 	min-width: 270px;
	// 	    flex: 1 0 270px;
	// }

