		padding: 25px 15px;
	}

	.form_wrapper {
		max-width: 275px;
	}

	// .content_left {
	// 	flex: 1 0 290px;
	// }
}



@media screen and (max-width: 980px) {
	h1.title {
		font-size: @header_fs * 0.95;
	}

	.content_left {
		flex: 1;
	}

	.flex-container {
		min-height: auto;
		height: auto;
	}

	.content_right {
		padding-left: 	0;
	}

	h2.sub_title {
	font-size: @sub_header * 0.95;

}

.content_right {
	height: auto;
	&:before {
		    min-height: 495px;
    left: -61%;
	}
}

.form_wrapper {
	margin-bottom: 	55px;
}

.content_left {
	margin-bottom: 	75px;
}

.main_list {
		background: none;
		padding: 0;
		border-radius: 0;
	}

.flex-container {
	flex-direction: column;
	padding: 30px 25px 0.5px 25px;
}

.content_left {
		max-width: 525px;
}

body 
{
	font-size: @main_fs * 0.95;
	
}
}

@media screen and (max-width: 770px) {
	// .content_left {
	// 	flex: 1 0 370px;
	// }

	// .content_right {
	// 	min-width: 300px;
	// 	padding-left: 0;
	// }


.content_right {
	&:before {
		 min-height: 420px;
		 left: -63%;
		 bottom: -56px;
	}
}

p.free {
	
	font-size: 27px;
}

	h1.title {
		br {
			display: none;
		}
	}

	.flex-container {
		padding: 30px  20px 0.5px 20px;
	}

	.content_right {
		margin-bottom: 	55px;
	}


	

	.form_wrapper {
		margin: 	0 auto;
	}
} 



@media screen and (max-width: 640px) {
	h1.title {
		font-size: @header_fs * 0.85;
	}

	h1.title br{
		display: none;
	}

	h2.sub_title {
	font-size: @sub_header * 0.85;


}

.center {
		padding: 0;
	}


body 
{
	font-size: @main_fs * 0.85;
	
}
