@media screen and (min-height: 1130px) {
	.form_wrapper {
		z-index: 7;
	}

	.content_right:before {
		display: none;
	}


}


@media screen and (max-height: 1130px) {
	.form_wrapper {
		z-index: 7;
	}

	body:before {
	   display: none;

}
}


@media screen and (max-height: 620px) {
		.content_right {
			height: 620px;
		}

		.main {
			height: 620px;
		}
}


@media (max-width: 980px) and (min-height: 1130px) {
	
	body:before {
		max-height: 700px;
	}
}